.card {
  min-width: 200px;
  min-height: 280px;
  flex: 0 1 10%;
  color: rgb(253, 252, 252);
  margin: 10px;
  perspective: 1000px;
}
.inner {
  width: 100%;
  height: 100%;
  transition: transform 1s;
  transform-style: preserve-3d;
  position: relative;
  border-radius: 10px;
}

.side {
  position: absolute;
  width: 100%;
  height: auto;
  backface-visibility: hidden;
  overflow: hidden;
  box-shadow: 0px 10px 13px -7px #000000, 5px 5px 15px 5px rgba(0, 0, 0, 0);
  border-radius: 10px;
}
.front_side {
  .icon {
    margin: 0 auto;
    display: block;
    width: 70px;
    height: auto;
  }
  .temp {
    margin: 5px;
    text-align: center;
  }
  .descr {
    margin-top: 0;
    padding-left: 5px;
    text-align: center;
  }
  .time {
    text-align: right;
    margin-top: 22px;
    padding-right: 5px;
  }
  .date {
    font-size: 18px;
    margin-top: 10px;
    text-align: right;
    padding-right: 5px;
  }

  .moreInfo {
    margin-top: 11px;
    float: right;
    padding-bottom: 5px;
  }
}
.navButtons {
  cursor: pointer;
  font-size: 18px;
  button {
    font-size: 18px;
    cursor: pointer;
    border: none;
    background-color: rgba(0, 0, 0, 0);
    color: white;
    &:focus {
      outline: none;
    }
  }
  &:hover {
    font-weight: bold;
  }
}
.city {
  margin-top: 30px;
  font-size: 20px;
  text-align: center;
  color: black;
  position: relative;
}
.city::before {
  content: '';
  display: block;
  position: absolute;
  top: -30px;
  left: 0;
  right: 0;
  bottom: -10px;
  z-index: -1;
  border-radius: 0 0 30% 30%;
  background: linear-gradient(to bottom left, lightblue 5%, whitesmoke 115%);
}
.back_side {
  height: auto;
  transform: rotateY(180deg);
  .moreData {
    text-align: center;
    padding: 2px;
    h3 {
      display: grid;
      grid-template: 1fr/1fr auto;
      margin: 17px 15px;
      text-align: start;
      span {
        flex: 1;
      }
    }
  }
  .backButton {
    float: left;
    padding-bottom: 5px;
  }
}
.isFlipped {
  transform: rotateY(-180deg);
}
.close {
  display: block;
  position: absolute;
  z-index: 1;
  padding: 10px;
  margin: -10px;
  right: 10px;
  top: 10px;
  font-size: 20px;
  cursor: pointer;
}

.card_cold {
  background: linear-gradient(to bottom, #61dcff 0%, #767fc5 100%);
}
.card_warm {
  background: linear-gradient(to bottom, #f6eade, #e29e0b);
}
